.JsonTree {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.JsonTree__Header {
  height: 90px;
  border-bottom: 1px solid #ccc;
  padding-left: 10px;
  overflow: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.JsonTree__Container {
  height: calc(100vh - 50px - 90px);  /* vh - input box height - header height */
  overflow: auto;
  padding: 5px 5px 0 25px;
  scrollbar-width: 1px;
}
.JsonTree__Container::-webkit-scrollbar, .JsonTree__Header::-webkit-scrollbar {
  width: 10px;
}
.JsonTree__Container::-webkit-scrollbar-thumb, .JsonTree__Header::-webkit-scrollbar-thumb {
  background-color: #bbb;
}
.JsonTree__Container::-webkit-scrollbar-track-piece, .JsonTree__Header::-webkit-scrollbar-track-piece {
  background-color: rgba(0, 0, 0, 0.1);

}
  
  
  
.JsonTree__Filter-Search {
  border-bottom: 1px solid #ddd;
  padding: 10px 0;
}
.JsonTree__Filter-Search input {
  padding: 4px;
  border-radius: 4px;
  border: 1px solid #ccc;
  color: #7a7a7a;
  outline: none;
  margin-right: 10px;
}
.JsonTree__Filter-Type {
  padding: 4px;
  color: rgb(80, 80, 80);
  font-size: 14px;
  font-family: Verdana, 'Lucida Sans Unicode', Geneva, sans-serif;
  cursor: pointer;
  white-space: nowrap;
}
.JsonTree__Filter-Type input[type='checkbox'] {
  margin-right: 5px;
  width: 15px;
  height: 15px;
  vertical-align: text-bottom;
}



.JsonTree__Customization-Buttons {
  padding: 10px 0;
}
.JsonTree__Customization-Buttons button {
  border: none;
  background-color: transparent;
  color: rgb(80, 80, 80);
  cursor: pointer;
  outline: none;
  font-family: Verdana, 'Lucida Sans Unicode', Geneva, sans-serif;
}
.JsonTree__Customization-Buttons button:not(:last-child) {
  margin-right: 20px;
}
.JsonTree__Customization-Buttons button:hover {
  text-decoration: underline;
  color: rgb(54, 54, 54);
}

  


.JsonTree__Object-Box {
  font-size: 14px;
  font-family: monospace;
}

.JsonTree__Object-Details {
  border: none;
}
.JsonTree__Object-Name {
  color: rgb(172, 0, 172);
  cursor: pointer;
}
.JsonTree__Symbol {
  display: inline-block;
  width: 20px;
  padding: 0 7.5px;
  font-size: 13px;
  margin-left: -20px;
  vertical-align: top;
}
.JsonTree__Object-Type {
  color: rgb(71, 71, 71);
}

.JsonTree__Object-Data {
  display: none;
  margin-left: 10px;
}d
.JsonTree__Key-string, .JsonTree__Key-number {
  color: rgb(172, 0, 172);
}
.JsonTree__Value-string {
  color: rgb(221, 59, 0);
}
.JsonTree__Value-number {
  color: blue;
}

.show {
  display: block;
}


